/**
 * @fileoverview Factory for ./service/cid-impl.js
 */

import {getElementService} from './service';

/**
 * @param {!Window} window
 * @return {!Promise<!Cid>}
 */
export function cidFor(window) {
  return getElementService(window, 'cid', 'amp-analytics');
};
